import React from 'react'
import { useNavigate } from 'react-router-dom';
import { useModal } from '../../contexts/ModalContext'

//   
import {sessionlogOut} from '../../session'
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from '../../redux/rootReducer';
import { logout } from '../../redux/store/userSlice'; // import  

const Header = () => {
  const { openModal } = useModal();
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const userInfo = useSelector((state: RootState) => state.user.userInfo); //  
  const isLoggedIn = useSelector((state : RootState) => state.user.isLoggedIn); // 

  return (
    <header>
      <div className='inner'>
          <p className='logo'>GOATGAM</p>
          <div className='header-util'>
              {
              !isLoggedIn ? <p onClick={()=>openModal('login')}></p>
              :
              <>
              <p onClick={()=>{
                dispatch(logout());
                sessionlogOut();
                navigate('/');
                }
                }></p>
              <p onClick={()=>navigate('/write', {state : { nickName : userInfo.nickName}})}></p>
              </>
              }
              
              
              
          </div>
      </div>
    </header>
  )
}

export default Header