import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useLocation, useNavigate, useParams } from 'react-router-dom';

import { Horizontal_2 } from '../components/layout/Horizontal';
import { Title } from '../components/ui/Title';
import Input from '../components/ui/Input';
import SelectBox from '../components/ui/SelectBox';
import { ButtonSt1 } from '../components/ui/Buttons';
import { isValidInput, isValidDiscordURL, formatDateToYYYYMMDD } from '../utils';
import CalendarCustom from '../components/ui/CalendarCustom';
import { format } from 'path';
import { formatDate } from 'react-calendar/dist/cjs/shared/dateFormatter';

const Write = () => {

  const today = new Date(); 
  const formattedDate = today.toLocaleDateString();

  const initialInputs = {
    title: '',
    genre: '',
    gameName: '',
    url: '',
    content:'',
    personnel : '1',
    deadLine : formatDateToYYYYMMDD(formattedDate),
    detailGenre : '',
    nickName : '',
    status : 0
  };
  
  const [information, setInformation] = useState(initialInputs)
  const [detailGenreArr, setdetailGenreArr] = useState<string[]>([]) //    State
  const {title, genre, gameName, url, content, personnel, deadLine, detailGenre, status} = information;
  const {state : {nickName}} = useLocation();
  const navigate = useNavigate();
  const { num } = useParams();

  //   
  const CalendarChange = (value: any, event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    setInformation({
      ...information,
      deadLine : formatDateToYYYYMMDD(value)
    })
  };

  //input onChange 
  const onChange = (e : React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    
    if(name === 'detailGenre'){
      const arr = value.split(",");

      if(arr.length !== 4) {
        setdetailGenreArr(arr)
      }else{
        alert('  3 .');
      }
    }

    setInformation({
      ...information,
      [name] : value
    })
  }

  //     information state .
  useEffect(()=>{
    const fetchData = async () => {
      if(num){
        try{
          const response = await axios.get(`${process.env.REACT_APP_SERVER_URL}/view/${num}`);
          if (response.data && response.data.length > 0) {
            setInformation(response.data[0]);
            const detail = response.data[0].detailGenre;
            const detail_arr = detail.split(",");
            
            setdetailGenreArr(detail_arr);
          } else {
            alert('   .');
          }
        } catch (error) {
          alert('    .');
        }
      }
    }
    fetchData();
  }, [num])

  //selectBoxHandler
  const SelectedHandler = (option : string) => {
    setInformation({...information, genre : option});
  }

  const exceptionHandler = () => {
    if (!isValidInput(genre)) {
      throw new Error('  .');
    }
  
    if (detailGenreArr.length === 0) {
      throw new Error('    .');
    }

    if (!isValidInput(gameName)) {
      throw new Error('  3  .');
    }

    if (!isValidDiscordURL(url)) {
      throw new Error(' URL .');
    }

    if (!isValidInput(title)) {
      throw new Error(' 3  .');
    }
    
    if(!isValidInput(content)) {
      throw new Error(' 3   .');
    }
    
  }

  // 
  const onClickHandler = async () => {
    try {
      exceptionHandler();

      const response = await axios.post(`${process.env.REACT_APP_SERVER_URL}/write`,{...information, nickName : nickName});
      if (response.status === 200) {
        navigate('/');
      } else {
        alert(' .');
      }
    } catch (error) {
      alert(error);
    }
  }

  // 
  const modifyHandler = async () => {
    try {
      exceptionHandler();

      // YYYY-MM-DD    
      const formattedDate = today.toISOString().slice(0, 10);
      if(status === 1 && new Date(formattedDate) < new Date(deadLine)){
        information.status = 0
      }

      const response = await axios.put(`${process.env.REACT_APP_SERVER_URL}/modify/${num}`,information);
      if (response.status === 200) {
        navigate(`/view/${num}`);

      } else {
        alert(' .');

      }
    } catch (error) {
      //  
      alert(error);
    }
  }

  return (
    <div className='inner'>
      <section>
        <Title title="1.    URL  ."/>
        <Horizontal_2>
          <li><SelectBox title='' options={['FPS', 'TPS', 'AOS', 'RPG', 'MOBILE']} onSelectOption={SelectedHandler} value={genre}/></li>
          <li>
            <Input name="detailGenre" title="" value={detailGenre} onChange={onChange}/>
            {
              detailGenreArr.length !== 0 &&
              <ul className='tag-wrap'>
                {detailGenreArr.map((param)=>{
                  return(
                    <li>{param}</li>
                  );
                })
              }
              </ul>
              
            }
          </li>
          <li><Input name="gameName" title="" value={gameName} onChange={onChange}/></li>
          <li><Input name="url" title=" URL" value={url} onChange={onChange}/></li>
          <li><Input name="personnel" title="" value={personnel} onChange={onChange}/></li>
          <li>
            <CalendarCustom title=" " onchange={CalendarChange} value={deadLine}/>
          </li>
        </Horizontal_2>
      </section>
      <section>
        <Title title="2.    ."/>
        <Input name="title" title="" value={title} onChange={onChange}/>
        <textarea className='text-area mg-t20' name='content' onChange={onChange} value={content}/>

        <div className='flx jsc'>
          {
            num ? <ButtonSt1 txt='' onClick={modifyHandler}/>
            :
            <ButtonSt1 txt='' onClick={onClickHandler}/>
          }
            
          </div>
      </section>
    </div>
  )
}

export default Write